import { h, FunctionalComponent } from 'preact'
import { useState, useEffect } from 'preact/hooks'
import './popup.scss'

export interface IPopupProps {
  id?: string
  message?: string
  btnYesTitle?: string
  btnNoTitle?: string
  redirectUrl: string
  timeout: number
}

const defaultMessage = ``

export const Popup: FunctionalComponent<IPopupProps> = (props) => {
  const { id, message, btnYesTitle, btnNoTitle, redirectUrl } = props
  const [visible, setVisible] = useState(false)

  /**
   * Popup automatically after timeout
   */
  useEffect(() => {
    const viewed = window.sessionStorage.getItem('popup_viewed')
    if (viewed === message) {
      return
    }
    const timer = setTimeout(() => {
      window.dataLayer = window.dataLayer || []
      window.dataLayer.push({
        event: 'popup-shown',
      })
      setVisible(true)
    }, props.timeout)
    return () => {
      clearTimeout(timer)
    }
  }, [])

  const onCloseClick = () => {
    setVisible(false)
  }

  const onNoClick = () => {
    const sessionValue = id || message
    window.sessionStorage.setItem('popup_viewed', sessionValue)
    setVisible(false)
  }

  const className = 'qwidget-popup'

  if (!visible) {
    return null
  }

  return (
    <div className={className}>
      <button onClick={onCloseClick} className={`${className}__close_button`}>
        &times;
      </button>
      <div className={`${className}__wrapper`}>
        <div className={`${className}__message`}>
          <p className={`${className}__message__text`}>{message || defaultMessage}</p>
        </div>
        <a
          href={redirectUrl}
          target="_blank"
          rel="noreferrer"
          className={`${className}__button ${className}__button--yes`}
        >
          {btnYesTitle || 'Yes'}
        </a>
        <a onClick={onNoClick} className={`${className}__button ${className}__button--no`}>
          {btnNoTitle || 'No thanks'}
        </a>
      </div>
    </div>
  )
}
